.app-wrapper {
  display: grid;
  width: auto;
  margin: 0auto;
  grid-template-areas:
    'h h'
    'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background-color: #fffafa;
  border-radius: 12px;
  margin: 5px;
}
